.catalog {
  margin-top: 50px;
   
  &__container {
    display: grid;
    grid-template-columns: repeat(auto-fit, $cardWidth);
    column-gap: $cardGap;
    justify-content: center;

    max-width: $catalogWidth;
    
    margin: 0 auto;
  }
  
  &__card {
    margin-bottom: 51px;
  }
}
